.fill-recruitment-container {
    background-color: var(--content-bg);
    .content-section {
        margin-top: 30px;
        .responsive-grid {
            width: 100% !important;
            margin-bottom: 0 !important;
        }
        .popular-text {
            font-family: 'Google Sans', sans-serif;
            font-size: 18px;
            color:var(--black);
        }
        .apply-method-section {
        width: 100%;
        padding: 28px;
        border-radius: 8px;
        background-color: var(--white);
        margin-top: 30px;
        }
        .main-content-section {
            flex-direction: row;
            justify-content: space-between;
            border-radius: 8px;
            background-color: var(--white);
            margin-top: 30px;
            .title-text {
                font-family: Google Sans;
                font-weight: bold;
                font-size: 28px;
            }
            .calendar_custom_container {
                width: 100%;
                position: absolute;
                padding: 10px;
                background-color: white;
                box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
                0 6px 20px 0 rgba(0, 0, 0, 0.19);
                border-radius: 5px;
                }
            .drop_custom_container {
                width: 100%;
                position: absolute;
                background-color: white;
                box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
                0 6px 20px 0 rgba(0, 0, 0, 0.19);
                z-index: 10;
                border: 1px solid;
            }
            .disable {
                margin-left: 15px;
                font-family: 'Google Sans', sans-serif;
                font-size: 16px;
                color: red;
            }
            .disable::placeholder {
                color: red;
                font-family: 'Google Sans', sans-serif;
            }
            .select-item {
                margin-top: 18px;
                height: 4.4em;
                width: 100%;
                background: #ffffff;
                border-radius: 8px;
                border: none;
                outline: none;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                padding: 10px;
                -webkit-appearance: none;
                -moz-appearance: none;
                text-indent: 1px;
                text-overflow: '';
                color: var(--text-placeholder);
                font-family: 'Google Sans', sans-serif;
                .option {
                    height: 40px;
                }
            }
            .custom-input {
                height: 127px;
                align-items: start;
            }
            .right-content-section {
                padding: 20px;
                width: 100%;
                display: flex;
                flex-direction: column;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                border-radius: 8px;
                margin-top: 20px;
                .contact-info-section {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    margin-top: 20px;
                    .right-content-title-text {

                    }
                    .contact-text {
                        font-family: 'Google Sans', sans-serif;
                        font-size: 18px;
                        margin-left: 17px;
                        color:var(--black);
                    }
                }
            }
            .btn-text {
                font-family: 'Google Sans', sans-serif;
                font-size: 18px;
                font-weight: bold;
                color: var(--white);
            }
        }
    }
    
};